.ExpContainer {
  padding-top: 80px;
  padding-left: 50px;
  padding-right: 50px;
}

.Item {
  width: 750px;
  padding-top: 25px;
  padding-bottom: 40px;
  display: flex;
  transition: background-color 0.3s ease;
  border-radius: 10px;
}

.Item:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.03);
}

.ItemDate {
  padding-left: 15px;
  min-width: 150px;
  margin-right: 10px;
  color: #6378a0;
  font-weight: bold;
}

.ItemPosition {
  color: #b9c2dc;
  font-weight: 600;
  font-size: 17px;
}

.Item:hover .ItemPosition {
  color: #72d4d6;
}

.ItemBody {
  color: #8d9bb8;
  padding-right: 30px;
}

.TechList {
  padding-top: 20px;
  display: flex;
}

.TechStack {
  margin-right: 10px;
  color: #72d4d6;
  background-color: #193346;
  padding: 6px 14px;
  font-size: 15px;
  border-radius: 40px;
  font-weight: 600;
}
